 import axios from '../axios';

let base =configURL.shipUrl;    // 

let base2 = configURL.infoUrl;   // 

let base3 = configURL.appUrl; //app



// 
export const start = params => { return axios.post(`${base}/shipPact/queryValueSetEle.json`, params).then(res => res.data); };

export const saveForm = params => { return axios.post(`${base}/pallet/save.json`, params).then(res => res.data); };

export const trader = params => { return axios.post(`${base}/traderNameList.json`, params).then(res => res.data); };

export const shipInfoData = params => { return axios.post(`${base}/sysShip/findSysShipByImoName.json`, params).then(res => res.data); };    //

export const queryValueSet = params => { return axios.post(`${base}/sysShip/queryValueSet.json`, params).then(res => res.data); };    // 

export const tideData = params => { return axios.post(`${base}/tide/findTideListByPortDate.json`, params).then(res => res.data); };    //

export const tideDataApp = params => { return axios.post(`${base3}/ship/tide/findTideListByPortDate.json`, params).then(res => res.data); };    //

export const conversionAllData = params => { return axios.post(`${base}/om/unit/findAll.json`, params).then(res => res.data); };    // - 

export const conversionData = params => { return axios.post(`${base}/om/unit/getRateInfo.json`, params).then(res => res.data); };    // - 

export const basicData = params => { return axios.post(`${base}/om/basicTariff/findBasicTariffByPort.json`, params).then(res => res.data); };    //

export const queryTrack = params => { return axios.post(`${base}/shipPact/queryTrack.json`, params).then(res => res.data); };    //

export const bootsInfo = params => { return axios.post(`${base}/shipPlate/findShipPlateDetailForground.json`, params).then(res => res.data); }; //

export const shippingCost = params => { return axios.post(`${base}/om/dealPoints/getDealPointsDates.json`, params).then(res => res.data); } // 

export const costLoad = params => { return axios.post(`${base}/om/basicTariff/findAllPortList.json`, params).then(res => res.data); } // 

export const CostSearch = params => { return axios.post(`${base}/om/basicTariff/freightTools.json`, params).then(res => res.data); } //

export const CostDates = params => { return axios.post(`${base}/om/dealPoints/getDealPointsDates.json`, params).then(res => res.data); } // 

export const bootsList = params => {return axios.post(`${base}/shipPlate/queryAllShipPlateList.json`, params).then(res => res.data);} // 

export const transMore = params => {return axios.post(`${base}/transit/queryMoreTransitList.json`, params).then(res => res.data);} // 

export const goodsList = params => { return axios.post(`${base}/pallet/findMorePalletList.json`, params).then(res => res.data);};   // 

export const goodsInfo = params => { return axios.post(`${base}/pallet/findPalletDetail.json`, params).then(res => res.data);};   // 


// 
export const topInfo = params => { return axios.post(`${base}/shipPlate/findShipPlateList.json`, params).then(res => res.data); };

export const dayInfo = params => { return axios.post(`${base}/om/dealPoints/getTodayDealPoints.json`, params).then(res => res.data); };

//export const oceanInfo = params => { return axios.post(`${base2}/front/infoList.json`, params).then(res => res.data); };  //
