//export const daysInfo = params => { return axios.post(`${base2}/front/infoList.json`, params).then(res => res.data); }; //

export const userLoad = params => { return axios.get(`${base}/current_user/get.json`, params).then(res => res.data);};   // 

export const userLoadInfo = params => { return axios.get(`${base2}/current_user/get.json`, params).then(res => res.data);};   // 

export const goodData = params => { return axios.post(`${base}/pallet/findPalletList.json`, params).then(res => res.data);};   // 

export const oceanInfo = params => { return axios.post(`${base2}/front/infoListOS.json`, params).then(res => res.data); };  //

export const daysInfo = params => { return axios.post(`${base2}/front/infoListOS.json`, params).then(res => res.data); }; //

export const mapgui = params => { return axios.post(`${base}/api/geojson/waypoint.json`, params).then(res => res.data); };

export const columnList = params => { return axios.post(`${base2}/column/columnList.json`, params).then(res => res.data); }; //

export const  dingYue = params => { return axios.post(`${base2}/subscribe/subscribeColumn.json`, params).then(res => res.data); }; //

export const  cancle = params => { return axios.post(`${base2}/subscribe/removeSubscribe.json`, params).then(res => res.data); }; //




